<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-right" style="margin-right: 10px">
        <el-button   @click="">保存</el-button>
      </div>
    </div>
    <div class="clearfix list-center">
      <el-col :span="12">
        <div class="clearfix list-button" >
          <div class="pull-left">
            <h4 class="list-button-text">联系人基本信息：</h4>
          </div>
        </div>
        <div class="clearfix list-center">
          <el-col :span="24">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
              <el-form-item label="联系人编号" >
                <el-input v-model="ruleForm.id" readonly v-if="ruleForm.editorId"></el-input>
                <p class="list-editor"  v-else >{{ruleForm.id}}</p>
              </el-form-item>
              <el-form-item label="姓名" prop="name">
                <el-input v-model="ruleForm.name"
                          @blur="editorForm(ruleForm.editorName,'editorName')"
                          v-focus="ruleForm.editorName"
                          v-if="ruleForm.editorName">
                </el-input>
                <p class="list-editor" @click="editorForm(ruleForm.editorName,'editorName')" v-else>
                  {{ruleForm.name}}
                </p>
              </el-form-item>
              <el-form-item label="性别" prop="sex">
                <el-select v-model="ruleForm.sex" v-if="ruleForm.editorSex" ref="inputRef"
                           @blur="editorForm(ruleForm.editorSex,'editorSex')"
                           v-focus="ruleForm.editorSex"
                           @visible-change="selectDropdown"
                           placeholder="请选择性别" style="display: block">
                  <el-option
                    v-for="item in sexOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
                <p class="list-editor" v-else @click="editorForm(ruleForm.editorSex,'editorSex')">
                  {{setValueLable(ruleForm.sex,sexOptions)}}
                </p>
              </el-form-item>
              <el-form-item label="所属客户" prop="person">
                <el-select v-model="ruleForm.person" v-if="ruleForm.editorPerson" ref="inputRef"
                           @blur="editorForm(ruleForm.editorPerson,'editorPerson')"
                           @visible-change="selectDropdown"
                           v-focus="ruleForm.editorPerson"
                           placeholder="请选择所属客户" style="display: block">
                  <el-option
                    v-for="item in personOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
                <p class="list-editor" v-else @click="editorForm(ruleForm.editorPerson,'editorPerson')">
                  {{ruleForm.person}}
                </p>
              </el-form-item>
              <el-form-item label="生日" >
                <el-date-picker v-model="ruleForm.date" type="date"
                                @blur="editorForm(ruleForm.editorDate,'editorDate')"
                                v-focus="ruleForm.editorDate"
                                v-if="ruleForm.editorDate"
                                placeholder="选择日期">
                </el-date-picker>
                <p class="list-editor" v-else @click="editorForm(ruleForm.editorDate,'editorDate')">
                  {{ruleForm.date | timeFormat}}
                </p>
              </el-form-item>
              <el-form-item label="角色" prop="role">
                <el-select v-model="ruleForm.role" v-if="ruleForm.editorRole" ref="inputRef"
                           @blur="editorForm(ruleForm.editorRole,'editorRole')"
                           @visible-change="selectDropdown"
                           v-focus="ruleForm.editorRole"
                           placeholder="请选择角色" style="display: block">
                  <el-option
                    v-for="item in roleOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
                <p class="list-editor" v-else @click="editorForm(ruleForm.editorRole,'editorRole')">
                  {{ruleForm.role}}
                </p>
              </el-form-item>
            </el-form>
          </el-col>
        </div>
      </el-col>
      <el-col :span="12" style="padding-left: 15px">
        <div class="clearfix list-button">
          <div class="pull-left">
            <h4 class="list-button-text">联系方式：</h4>
          </div>
        </div>
        <div class="clearfix list-center">
          <!--列表-->
          <el-table :data="linkTableData"  v-loading="listLoading" @selection-change="" class="chanceTable">
            <el-table-column type="way" label="联系人方式"  align="center">
            </el-table-column>
            <el-table-column prop="type" label="类型" align="center" >
            </el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"  >
            </el-table-column>
            <el-table-column label="操作" width="180" align="center">
              <template scope="scope">
                <el-button size="small" @click="">查看</el-button>
                <el-button type="danger" size="small" @click="">删除</el-button>
              </template>
            </el-table-column>
          </el-table>

          <!--工具条-->
          <el-col :span="24" class="toolbar">
            <el-col :span="20" style="height:32px">
              <el-pagination background layout="prev, pager, next,total" @current-change="" :page-size="10" :total="total">
              </el-pagination>
            </el-col>
            <el-col :span="4">
              <div class="clearfix list-button" style="margin: 0">
                <div class="pull-right" style="margin-right: 20px">
                  <el-button   @click="" >新增联系方式</el-button>
                </div>
              </div>
            </el-col>
          </el-col>
        </div>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">快递地址：</h4>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--列表-->
      <el-table :data="tableData"  v-loading="listLoading" @selection-change="" class="chanceTable">
        <el-table-column prop="address" label="地址" align="center" >
        </el-table-column>
        <el-table-column prop="remarks" label="备注"  align="center"  >
        </el-table-column>
        <el-table-column label="操作" width="180" align="center">
          <template scope="scope">
            <el-button size="small" @click="">查看</el-button>
            <el-button type="danger" size="small" @click="">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-col :span="20" style="height:32px">
          <el-pagination background layout="prev, pager, next,total" @current-change="" :page-size="10" :total="total">
          </el-pagination>
        </el-col>
        <el-col :span="4">
          <div class="clearfix list-button" style="margin: 0">
            <div class="pull-right" style="margin-right: 20px">
              <el-button   @click="" >新增快递地址</el-button>
            </div>
          </div>
        </el-col>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">备注：</h4>
      </div>
    </div>
    <div class="list-center-bottom" >
      <u-editor></u-editor>
    </div>
  </div>
</template>
<script>
  import UEditor from '@/components/layout/ueditor/UEditor'
  export default {
    name: 'chanceDetail',
    components: {
      UEditor
    },
    data () {
      return {
        total: 0,
        page: 10,
        listLoading: false,
        linkTableData: [],
        tableData: [],
        ruleForm: {
          id: '',
          editorId: true,
          name: '',
          editorName: true,
          sex: 'sex1',
          editorSex: true,
          person: '',
          editorPerson: true,
          date: '',
          editorDate: true,
          role: '',
          editorRole: true
        },
        rules: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '请选择性别', trigger: 'change' }
          ],
          person: [
            { required: true, message: '请选择所属客户', trigger: 'change' }
          ],
          role: [
            { required: true, message: '请选择角色', trigger: 'change' }
          ]
        },
        sexOptions: [
          {
            value: 'sex1',
            label: '男'
          },
          {
            value: 'sex2',
            label: '女'
          }
        ],
        personOptions: [],
        roleOptions: [],
        type: 'create',
        dropdown: false
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      editorForm (value, obj) {
        if (this.type === 'detail') {
          if (this.dropdown === true) {
            this.ruleForm[obj] = value
          } else {
            this.ruleForm[obj] = !value
            if (event.type === 'blur') {
              if (obj === 'editorName') {
                if (this.ruleForm.name === '') {
                  this.ruleForm[obj] = value
                }
              }
            }
          }
        }
      },
      setValueLable (state, arr) {
        let label = ''
        arr.forEach(function (val) {
          if (val.value === state) {
            label = val.label
          }
        })
        return label
      },
      selectDropdown (value) {
        this.dropdown = value
        if (!value) {
          this.$refs.inputRef.focus()
        }
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
      if (this.$route.query.type === 'detail') {
        this.type = 'detail'
        this.ruleForm = {
          id: '1',
          editorId: false,
          name: '李四',
          editorName: false,
          sex: 'sex1',
          editorSex: false,
          person: 'xxxx公司',
          editorPerson: false,
          date: new Date(),
          editorDate: false,
          role: '项目经理',
          editorRole: false
        }
      }
    },
    mounted () {
    }
  }
</script>



